/* eslint-disable no-irregular-whitespace */
import { openDB } from 'idb'
import CONFIG from '../globals/config'

const{ DATABASE_NAME, DATABASE_VERSION, OBJECT_STORE_NAME } = CONFIG

constdbPromise = openDB(DATABASE_NAME, DATABASE_VERSION, {
  upgrade (database) {
    database.createObjectStore(OBJECT_STORE_NAME, { keyPath:'id'})
  }
})

const FavoriteRestaurantIdb = {
  asyncgetRestaurant (id) {
    return(awaitdbPromise).get(OBJECT_STORE_NAME, id)
  },
  asyncgetAllRestaurant () {
    return(awaitdbPromise).getAll(OBJECT_STORE_NAME)
  },
  asyncputRestaurant (restaurant) {
    return(awaitdbPromise).put(OBJECT_STORE_NAME, restaurant)
  },
  asyncdeleteRestaurant (id) {
    return(awaitdbPromise).delete(OBJECT_STORE_NAME, id)
  }
}

export default FavoriteRestaurantIdb
